 * To find all icon codes see the Font Awesome cheatsheet -
 * http://fortawesome.github.io/Font-Awesome/cheatsheet/
 *
 * AT does not import all of fontawesome, only what we need to support more
 * recent browsers:
 *  - woff: Chrome 6+, Firefox 3.6+, IE 9+, Safari 5.1+, Android 4.4+
 *  - ttf: Chrome 4+, Firefox 3.5, Opera 10+, Safari 3â€”5, Android
 *
 * Installing local fonts:
 *
 * NOTE: due to licencing restrictions imposed by Drupal.org we must load
 * the font files from a CDN. It is recommended you install the font files
 * locally on your server (in your theme) and link to those instead.
 *
 * To install locally:

 * First download FontAwesome from Github (link above), check the version
 * this theme shipped with ($fa-version) or view the generated CSS and look
 * for the cache identifier string at the end of the font file path, e.g.
 * "?v=4.6.3". You need to download the version this theme shipped with or
 * newer.
 *
 * Next create the directory: "styles/css/fonts", then place the .woff
 * and .ttf files in the "fonts" directory.
 *
 * If you are using SASS you can uncomment the $fa-font-path !default
 * in uikit > components > font-awesome > _variables.scss and comment
 * out the Bootstrap CDN path. Amend the
 *
 * If you are using CSS directly, just edit the path manually to point to
 * the files, e.g.:
 * src: url("../fonts/fontawesome-webfont.woff")
*/
@font-face {
  font-family: 'FontAwesome';
  src: url("//cdnjs.cloudflare.com/ajax/libs/font-awesome/4.6.3/fonts/fontawesome-webfont.eot?v=4.6.3");
  src: url("//cdnjs.cloudflare.com/ajax/libs/font-awesome/4.6.3/fonts/fontawesome-webfont.eot?#iefix&v=4.6.3") format("embedded-opentype"), url("//cdnjs.cloudflare.com/ajax/libs/font-awesome/4.6.3/fonts/fontawesome-webfont.woff2?v=4.6.3") format("woff2"), url("//cdnjs.cloudflare.com/ajax/libs/font-awesome/4.6.3/fonts/fontawesome-webfont.woff?v=4.6.3") format("woff"), url("//cdnjs.cloudflare.com/ajax/libs/font-awesome/4.6.3/fonts/fontawesome-webfont.ttf?v=4.6.3") format("truetype"), url("//cdnjs.cloudflare.com/ajax/libs/font-awesome/4.6.3/fonts/fontawesome-webfont.svg?v=4.6.3#fontawesomeregular") format("svg");
  font-weight: normal;
  font-style: normal;
  font-stretch: normal;
}
.fa {
  display: inline-block;
  font-family: FontAwesome, sans-serif;
  font-size: inherit;
  text-rendering: auto;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.fa-none {
  font-size: inherit;
}

.fa-lg {
  font-size: 26px;
  font-size: 1.625rem;
  line-height: 30px;
  line-height: 1.875rem;
}

.fa-2x {
  font-size: 32px;
  font-size: 2rem;
  line-height: 36px;
  line-height: 2.25rem;
}

.fa-3x {
  font-size: 48px;
  font-size: 3rem;
  line-height: 51px;
  line-height: 3.1875rem;
}

.fa-4x {
  font-size: 64px;
  font-size: 4rem;
  line-height: 69px;
  line-height: 4.3125rem;
}

.fa-5x {
  font-size: 80px;
  font-size: 5rem;
  line-height: 84px;
  line-height: 5.25rem;
}

.fa-fw {
  width: 26px;
  width: 1.625rem;
}

/* Web font smoothing */
:after,
:before {
  text-rendering: auto;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
